import React, { Component } from 'react'
export class Clear extends Component {

    onSubmit = (e) => {
        if (window.confirm("Are you sure to delete all the todos?")) {
            localStorage.clear();
        }
        
    }

    render() {
        return (
            <div>
                <form id='textForm' onSubmit={this.onSubmit} style={{ display: 'flex' }}>
                    <input type='submit'
                        value='Clear All'
                        className='btnclear'
                        style={{ boxAlign: 'center'}}
                    />
                </form>
            </div>
        )
    }
}

export default Clear;
