import React, { Component } from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom'
import Header from './component/layout/Header'
import Todos from './component/Todos';
import AddTodo from './component/AddTodo';
import Clear from './component/Clear';
import uuid from "uuid";
import About from './component/pages/About';
import './App.css';
import axios from 'axios';

class App extends Component {
    state = {
        todos: [
            // {
            //   id: uuid.v4(),
            //   title: 'Task to be completed',
            //   complete: false 
            // }
        ]
    }

    componentDidMount() {
        axios.get('https://jsonplaceholder.typicode.com/todos/?_limit=5')
            .then(response => {
                //console.log(response.data)
                //this.setState({todos: response.data})

            })
        const elems = [];
        if (localStorage.length === 0) {
            this.setState({
                todos: [{
                    id: uuid.v4(),
                    title: 'Task to be completed',
                    complete: false
                }]

            })
        } else {
            for (var i = 0; i < localStorage.length; i++) {
                var key = localStorage.key(i);
                var value = localStorage.getItem(key);
                //console.log('Key: ' + key + ', Value: ' + value);
                var parsed = JSON.parse(value)
                console.log(parsed)
                elems.push(parsed);
            }
            console.log(elems)
            this.setState({ todos: elems })
        }

    }
    markComplete = (id) => {
        var retrievedObject = localStorage.getItem(id);
        var retrievedObjectParsed = JSON.parse(retrievedObject);
        localStorage.removeItem(id)
        this.setState({
            todos: this.state.todos.map(todo => {
                if (todo.id === id) {
                    todo.complete = !todo.complete
                }
                return todo
            })
        })
        const updateTodo = {
            id: id,
            title: retrievedObjectParsed.title,
            complete: !retrievedObjectParsed.complete
        }
        localStorage.setItem(id, JSON.stringify(updateTodo));
    }

    //Delete Todo
    delTodo = (id) => {
        console.log(id)
            // axios.delete(`https://jsonplaceholder.typicode.com/todos/${id}`)
            // .then(response =>   this.setState({todos: [...this.state.todos.filter(todo => todo.id !== id)]})
            // )
        localStorage.removeItem(id)
        this.setState({ todos: [...this.state.todos.filter(todo => todo.id !== id)] })
    }

    addTodo = (addTitle) => {
            if (addTitle) {
                const newTodo = {
                    id: uuid.v4(),
                    title: addTitle,
                    complete: false
                }
                console.log(JSON.stringify(newTodo))
                localStorage.setItem(newTodo.id, JSON.stringify(newTodo));
                this.setState({ todos: [...this.state.todos, newTodo] });
            }


            // axios.post(`https://jsonplaceholder.typicode.com/todos/`,{
            //   title: addTitle,
            //   complete: false
            // })
            // .then(response => this.setState({todos: [...this.state.todos, response.data]}));
        }
        //Add Todo

    render() {
        return ( <Router>
            <div className = "App">
            <div className = 'container'>
            <Header/>
            <Route exact path = '/mytodos/'
            render = {
                pros => ( <React.Fragment>
                    <AddTodo addTodo = { this.addTodo }/> 
                    <Todos todos = { this.state.todos }
                    markComplete = { this.markComplete }
                    delTodo = { this.delTodo }/> 
                    <Clear/>
                    </React.Fragment>
                )
            }/> 
            <Route path = '/mytodos/about' component = { About }/> 
            </div > 
            </div>
            </Router>
        );
    }
}

export default App;