import React, { Component } from 'react'
import PropTypes from 'prop-types'
export class AddTodo extends Component {
    state ={
        title: ''
    }

    onChange = (e) => this.setState({[e.target.name]: 
        e.target.value});
        
    onSubmit = (e) => {
        const data = e.target.title.value
        if (data === ""){
            alert('Enter todos in text box')
        }
        e.target.title.value=''
        e.preventDefault();
        this.props.addTodo(this.state.title);
        this.setState({title: ''});
    }

    render() {
        return (
            <div>
           <form id = 'textForm' onSubmit ={this.onSubmit} style={{display: 'flex'}}>
               <input type = 'text' 
                      name ='title' 
                      autoFocus
                      style ={{flex: '10' , padding: '15px' , fontSize: '15px'}}
                      placeholder = 'Enter to Add Todos ...'
                      value={this.state.value}
                      onChange ={this.onChange}
                />
                <input type = 'submit' 
                value = 'Submit' 
                className = 'btn'
                style ={{flex: '1'}}  
                onClick={this.clear}  
                />
           </form>
            </div>
        )
    }
}

AddTodo.propTypes = {
    addTodo: PropTypes.func.isRequired
}

export default AddTodo;
